job-search-results {
  background: white;
  display: block;
  border-radius: $mainBorderRadius;
  box-shadow: $mainBoxShadow;
  -webkit-box-shadow: $mainBoxShadow;
  -moz-box-shadow: $mainBoxShadow;

  .jobs-count {
    text-align: center;
    padding: 25px;
    margin: 0 15px;
    color: white;
    background-color: $oechrBlueColor;
  }

  h2 {
    padding-left: 20px;
  }
}